.featured{
    height: 90vh;
    position:relative;
    .category{
        position:absolute;
        top: 80px;
        left: 50px;
        font-size: 30px;
        font-weight: 500;
        color:#fff;
        display: flex;
        align-items: center;
        select{
            cursor: pointer;
            background-color: var(--main-color);
            border:1px soid #fff;
            color:#fff;
            margin-left: 20px;
            padding:5px;
        }
    }
    img{
        width:100%;
        height:100%;
        object-fit: cover;
    }
    .info{
        width: 35%;
        position: absolute;
        left: 50px;
        bottom: 100px;
        color: #fff;
        display: flex;
        flex-direction: column;
        img{
            width: 400px;
            // svg 이미지 색상 변환
            filter: invert(98%) sepia(98%) saturate(0%) hue-rotate(342deg) brightness(101%) contrast(105%);
        }
        .desc{
            margin:20px 0px;
            font-size: 16px;
        }
        .buttons{
            display: flex;
            button{
                padding: 10px 20px;
                border: none;
                border-radius: 5px;
                display: flex;
                align-items: center;
                justify-content: center;
                font-size: 18px;
                font-weight: 500;
                margin-right: 10px;
                cursor: pointer;
                &.play{
                    background-color: #fff;
                    color:var(--main-color);
                }
                &.more{
                    background-color: gray;
                    color: #fff;
                }
                span{
                    margin-left: 5px;
                }
            }
        }
    }
}